

.header {
    background-color: red;
    height: 50px;
    width: 100%;
    position: fixed;
    top: 0;
    
    
    display: flex;
    align-items: center;
    flex-direction: row;
    justify-content: space-between;
    
}

.header > * {
    margin: 0 20px;
}

.header-input {
    display: flex;
    align-items: center;

    color: white;
    background-color: tomato;
    border-radius: 5px;
    height: 35px;
    
}

.header-input input[type=text]{
    background-color: tomato;
    color: white;
    border: 0;
    box-sizing: content-box;
    padding-left: 5px;
    width: 200px;
}

.header-input input[type=text]::placeholder{
    color: white;
}

.header-input input[type=text]:focus{
    outline-style: none;
}

.right-container {
    display: flex;
    align-items: center;
}

/* Switch Style */
.switch {
    position: relative;
    display: inline-block;
    width: 60px;
    height: 34px;
  }
  
  /* Hide default HTML checkbox */
  .switch input {
    opacity: 0;
    width: 0;
    height: 0;
  }
  
  /* The slider */
  .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ca2115;
    -webkit-transition: .4s;
    transition: .4s;
  }
  
  .slider:before {
    position: absolute;
    content: "";
    height: 26px;
    width: 26px;
    left: 4px;
    bottom: 4px;
    background-color: white;
    -webkit-transition: .4s;
    transition: .4s;
  }
  
  input:checked + .slider {
    background-color: #2196F3;
  }
  
  input:focus + .slider {
    box-shadow: 0 0 1px #2196F3;
  }
  
  input:checked + .slider:before {
    -webkit-transform: translateX(26px);
    -ms-transform: translateX(26px);
    transform: translateX(26px);
  }
  
  /* Rounded sliders */
  .slider.round {
    border-radius: 34px;
  }
  
  .slider.round:before {
    border-radius: 50%;
  }

